* {
  box-sizing: border-box;
}

body {
  background-color: #e5e5e5;
  margin: 0;
  font-family: roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16;
}

img {
  display: block;
}

ul {
  padding: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

.container {
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

.section {
  padding-top: 20px;
  padding-bottom: 40px;
}

.card-img {
  width: 280px;
  height: 398px;
  border-radius: 5px;
}

.gallery-card :hover {
  transition: transform .8s .2ms;
  transform: scale(1.1);
}

.card-title {
  text-transform: uppercase;
  color: #000;
  margin-top: 8px;
  margin-bottom: 8px;
  margin-right: auto;
}

.card-average {
  width: 40px;
  height: 20px;
  text-align: center;
  color: #fff;
  background: #ff6b08;
  border-radius: 5px;
  align-items: center;
  padding: 1px 6px;
  font-size: 12px;
  display: flex;
}

.card-release {
  color: #ff6b08;
  border-left: 1px solid orange;
  padding-left: 3px;
}

.card-ganres, .card-release {
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16;
}

.gallery-list {
  flex-wrap: wrap;
  gap: 32px;
  display: flex;
}

.card-item {
  width: 280px;
  height: 438px;
}

@media screen and (max-width: 767px) {
  .gallery-list {
    justify-content: center;
    align-items: center;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .card-average {
    width: 40px;
    height: 20px;
  }

  .card-img {
    width: 336px;
    height: 455px;
  }

  .section {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .card-item {
    width: calc(50% - 16px);
    height: 495px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }

  .card-img {
    width: 394px;
    height: 574px;
  }

  .gallery-wrapper {
    width: 1216px;
    gap: 16px;
  }

  .card-item {
    width: calc(33.3333% - 10.6667px);
    height: 634px;
  }

  .gallery-list {
    gap: 16px;
  }
}

